/* pages.css */

/* Style the main container for the About page */
.about-container {
  max-width: 1200px; /* Adjust the maximum width as needed */
  margin: 0 auto;
  padding: 20px;
}

/* Style each section within the About page */
.about-section {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 40px; /* Adjust the spacing between sections */
}

/* Style the text content in each section */
.about-content {
  flex: 1; /* Make text content take up available space */
  padding: 20px; /* Add padding around text content */
}

/* Style the subtitles in each section */
.about-subtitle {
  font-size: 24px; /* Set the font size for subtitles */
  color: #333; /* Set the text color */
  margin-bottom: 10px; /* Add spacing below subtitles */
}

/* Style the paragraph text in each section */
.about-text {
  font-size: 16px; /* Set the font size for paragraphs */
  line-height: 1.5; /* Set line height for readability */
  color: #666; /* Set the text color */
}

/* Style the images in each section */
.about-image {
  flex: 1; /* Make images take up available space */
  text-align: center; /* Center align images */
}

/* Style images to fit the container */
.about-image img {
  max-width: 100%; /* Ensure images don't exceed their container */
  height: auto; /* Maintain aspect ratio */
  border-radius: 8px; /* Add a rounded border to images */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Add a subtle box shadow */
}

/* Customize the AOS animations for fade-in effects */
[data-aos='fade-right'] {
  transform-origin: right;
  opacity: 0;
  transition: transform 0.6s ease, opacity 0.6s ease;
}

[data-aos='fade-left'] {
  transform-origin: left;
  opacity: 0;
  transition: transform 0.6s ease, opacity 0.6s ease;
}

/* Define the animation behavior on scroll */
[data-aos='fade-right'].aos-animate,
[data-aos='fade-left'].aos-animate {
  transform: translateX(0);
  opacity: 1;
}
/* Add media query for mobile screens */
@media (max-width: 768px) {
  /* Adjust styles for smaller screens here */
  .about-container {
    padding: 10px; /* Reduce padding for smaller screens */
  }

  /* Example: Center-align subtitles on smaller screens */
  .about-subtitle {
    text-align: center;
  }
}
/* pages.css */

//* pages.css */

/* Style the main container for the Team page */
.TeamPage {
  background: linear-gradient(to bottom, #007bff, #0056b3); /* Gradient background with blue tones */
  color: #fff; /* White text color for better contrast */
  padding: 40px 0; /* Increased padding for better spacing */
}

/* Center the header */
.Header {
  text-align: center;
  margin-bottom: 60px; /* Increased margin for more separation */
}

/* Style the title with an underline and increased font size */
.Title {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 20px;
  text-decoration: underline;
}

/* Style the main content section */
.Main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

/* Style each team member section */
.Section {
  flex-basis: calc(33.33% - 40px);
  margin-bottom: 40px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  text-align: center;
  padding: 30px; /* Increased padding for better spacing */
}

/* Style team member images with a subtle border and shadow */
.Image {
  width: 150px;
  height: 150px;
  border: 3px solid #007bff; /* Blue border color */
  border-radius: 50%;
  margin: 0 auto 20px;
  object-fit: cover;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

/* Style team member names with a slightly larger font size */
.Name {
  font-size: 26px;
  font-weight: bold;
  margin: 0;
  color: #333;
}

/* Style team member roles with a larger font size and color */
.Role {
  font-size: 18px;
  color: #555;
  margin: 5px 0;
}

/* Style team member descriptions with increased line height and color */
.Description {
  font-size: 16px;
  line-height: 1.6; /* Increased line height for readability */
  color: #666; /* Slightly darker text color */
  margin-top: 20px; /* Added margin at the top for spacing */
}

/* Style social media icons with a larger font size and color */
.SocialMediaIcon {
  font-size: 28px;
  margin: 10px;
  color: #007bff;
  text-decoration: none; /* Remove underlines from social media links */
}

/* Add a hover effect to social media icons */
.SocialMediaIcon:hover {
  color: #0056b3; /* Darker blue on hover */
}

/* Update the media query for better mobile responsiveness */
@media (max-width: 768px) {
  /* Stack profiles vertically */
  .Section {
    flex-basis: calc(100% - 20px);
  }

  /* Increase font size for better readability on smaller screens */
  .Name {
    font-size: 24px;
  }

  .Role {
    font-size: 16px;
  }

  .Description {
    font-size: 14px;
    line-height: 1.5;
  }
}
