/* EarlyAccessModal.css */

/* CSS for modal overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

/* CSS for the modal itself */
.modal {
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  max-width: 80%;
  width: 400px;
  text-align: center;
  position: relative;
}

/* CSS for the close button */
.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
}

/* CSS for modal content */
.modal-content {
  padding: 20px;
}

/* Media query for mobile responsiveness */
@media (max-width: 768px) {
  /* Adjust modal content for mobile */
  .modal-content {
    margin: 10% auto;
    padding: 15px;
  }
}


* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
}

a {
  color: unset;
  text-decoration: none;
}

.gradient__bg {

  /* safari 5.1+,chrome 10+ */

  /* opera 11.10+ */

  /* ie 10+ */

  /* global 92%+ browsers support */
  background:radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
}

.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section__padding {
  padding: 4rem 6rem;
}

.section__margin {
  margin: 4rem 6rem;
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media screen and (max-width: 700px) {
  .section__padding {
    padding: 4rem;
  }

  .section__margin {
    margin: 4rem;
  }
}

@media screen and (max-width: 550px) {
  .section__padding {
    padding: 4rem 2rem;
  }

  .section__margin {
    margin: 4rem 2rem;
  }  
}
/* Privacy.css */

/* Container styles */
.privacy-container {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

/* Header styles */
.privacy-header {
  text-align: center;
  margin-bottom: 20px; /* Increased margin for separation */
}

.privacy-illustration {
  max-width: 100%;
  height: auto;
}

.privacy-heading {
  font-size: 32px; /* Larger font size for the heading */
  margin: 20px 0;
  color: #007BFF; /* Brand color for the heading */
}

/* Section styles */
.privacy-subheading {
  font-size: 24px;
  margin-top: 30px;
  margin-bottom: 10px;
  color: #007BFF; /* Brand color for section headings */
}

.privacy-icon {
  width: 24px;
  height: 24px;
  margin-right: 10px;
  vertical-align: middle; /* Align icons vertically with text */
}

.privacy-text {
  font-size: 18px; /* Increased font size for better readability */
  line-height: 1.6; /* Increased line height for readability */
  margin-bottom: 15px;
  color: #333; /* Grey color for standard text */
}

.privacy-list {
  list-style-type: disc;
  padding-left: 20px;
}

.privacy-link {
  color: #007BFF; /* Brand color for links */
  text-decoration: underline;
}

/* Responsive styles for mobile */
@media (max-width: 768px) {
  .privacy-container {
    padding: 15px; /* Smaller padding for mobile */
  }

  .privacy-heading {
    font-size: 28px; /* Slightly smaller font size for mobile */
  }

  .privacy-text {
    font-size: 16px; /* Restore font size for mobile */
  }
}


/* Disclaimer.css */

/* Overall container styles */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  font-family: 'Roboto', sans-serif;
  background-color: #f8f8f8;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

/* Disclaimer heading styles */
.disclaimer-heading {
  text-align: center;
  font-size: 28px;
  font-weight: 700;
  color: #333;
  margin-bottom: 20px;
}

/* General text styles */
.general-text {
  font-size: 18px;
  line-height: 1.6;
  color: #555;
}

/* Section heading styles */
.section-heading {
  font-size: 24px;
  font-weight: 600;
  color: #333;
  margin-top: 30px;
}

/* Divider between sections */
.section-divider {
  margin-top: 30px;
  margin-bottom: 30px;
  border-top: 1px solid #ddd;
}

/* Link styles */
a {
  color: #007bff;
  text-decoration: none;
  transition: color 0.3s;
}

a:hover {
  color: #0056b3;
}

/* Add some box shadow and padding to links */
a {
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);
  padding: 2px 4px;
  border-radius: 3px;
  transition: box-shadow 0.3s, -webkit-transform 0.3s;
  transition: box-shadow 0.3s, transform 0.3s;
  transition: box-shadow 0.3s, transform 0.3s, -webkit-transform 0.3s;
}

a:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

/* Style the last updated date */
.last-updated {
  font-size: 14px;
  color: #777;
  text-align: right;
  margin-top: 20px;
}

/* Media query for mobile responsiveness */
@media (max-width: 768px) {
  .container {
    padding: 15px;
    border-radius: 0;
  }

  /* Adjust font sizes for smaller screens */
  .disclaimer-heading {
    font-size: 24px;
  }

  .general-text {
    font-size: 16px;
  }

  .section-heading {
    font-size: 20px;
  }
}
